// 保存结算设置
export function saveCommissionSetting(content = {}) {
  return new Promise((resolve, reject) => {
    service
      .setting('post', {
        keyword: 'commission_setting',
        content: content
      })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

// 获取结算设置
export function getCommissionSetting() {
  return new Promise((resolve, reject) => {
    service
      .search(
        'post',
        { include: 'setting' },
        {
          keyword: 'commission_setting'
        }
      )
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        $message(error.data.message);
        reject();
      });
  });
}

